














import ut from '../../utils/index.js';
export default {
	data() {
		return {
			headurl:wx.getStorageSync('headurl'),
			userinf:{}
		}
	},
	onShow() {
		this.userinf=wx.getStorageSync('userinf')||{}
	},
	methods: {
		go_mine_adress(){
			wx.navigateTo({
				url: '../mine/adress'
			})
		},
		uploadimg(){
			ut.uploadimg({
				callback:res=>{
					wx.setStorageSync('headurl',res);
					this.headurl=res;
				}
			})
		},
		go_mine_updatename(){
			wx.navigateTo({
				url: '../mine/updatename'
			})
		}
	}
}



